import React, {
    FormEvent,
    ChangeEvent,
    FunctionComponent,
    useEffect,
    useState,
  } from 'react';
  import { useDispatch, useSelector } from 'react-redux';
  import { RootState } from '../../modules';
  import { changeField, initializeForm, registerAsync } from '../../modules/auth';
  import AuthForm from '../../components/auth/AuthForm';
  import { checkAsync } from '../../modules/user';
  import { withRouter, RouteComponentProps } from 'react-router-dom';
  
  const RegisterForm: FunctionComponent<RouteComponentProps> = ({ history }) => {
    const [error, setError] = useState<string>('');
    const dispatch = useDispatch();
    const { form, auth, authError, user } = useSelector((state: RootState) => ({
      form: state.auth.register,
      auth: state.auth.auth,
      authError: state.auth.authError,
      user: state.user.user,
    }));
  
    //ì¸í’‹ ë³€ê²½ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬
    const onChange = (e: ChangeEvent<HTMLInputElement>) => {
      const { value, name } = e.target;
      dispatch(
        changeField({
          form: 'register',
          key: name,
          value,
        }),
      );
    };
  
    //í¼ ë“±ë¡ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬
    const onSubmit = (e: FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      const { username, password, passwordConfirm } = form;
      // í•˜ë‚˜ë¼ë„ ë¹„ì–´ ìˆë‹¤ë©´
      if ([username, password, passwordConfirm].includes('')) {
        setError('ë¹ˆ ì¹¸ì„ ëª¨ë‘ ì…ë ¥í•˜ì„¸ìš”.');
        return;
      }
      // ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ì§€ ì•ŠëŠ”ë‹¤ë©´)
      if (password !== passwordConfirm) {
        setError('ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.');
        dispatch(changeField({ form: 'register', key: 'password', value: '' }));
        dispatch(
          changeField({ form: 'register', key: 'passwordConfirm', value: '' }),
        );
        return;
      }
      dispatch(registerAsync.request({ username, password }));
    };
  
    //ì»´í¬ë„ŒíŠ¸ê°€ ì²˜ìŒ ë Œë”ë§ë  ë•Œ formì„ ì´ˆê¸°í™”í•¨
    useEffect(() => {
      dispatch(initializeForm());
    }, [dispatch]);
  
    // íšŒì›ê°€ì… ì„±ê³µ/ì‹¤íŒ¨ ì²˜ë¦¬
    useEffect(() => {
      if (authError) {
        // ê³„ì •ëª…ì´ ì´ë¯¸ ì¡´ì¬í•  ë•Œ
        // if (authError.response.status === 409) {
        //   setError('ì´ë¯¸ ì¡´ì¬í•˜ëŠ” ê³„ì •ëª…ì…ë‹ˆë‹¤.');
        //   return;
        // }
        // ê¸°íƒ€ ì´ìœ 
        setError('íšŒì›ê°€ì… ì‹¤íŒ¨');
        return;
      }
      if (auth) {
        console.log('íšŒì›ê°€ì… ì„±ê³µ');
        // console.log(auth);
        dispatch(checkAsync.request(''));
      }
    }, [auth, authError, dispatch]);
  
    // user ê°’ì´ ì˜ ì„±ì •ë˜ì—ˆëŠ”ì§€ í™•ì¸
    useEffect(() => {
      if (user) {
        console.log('check API ì„±ê³µ');
        console.log(user);
        history.push('/'); // í™ˆ í™”ë©´ìœ¼ë¡œ ì´ë™
        try {
          localStorage.setItem('user', JSON.stringify(user));
        } catch (e) {
          console.log('localStorage is not working');
        }
      }
    }, [history, user]);
  
    return (
      <AuthForm
        type="register"
        form={form}
        onChange={onChange}
        onSubmit={onSubmit}
        error={error}
      />
    );
  };
  
  export default withRouter(RegisterForm);
  