import React, {
  FormEvent,
  ChangeEvent,
  FunctionComponent,
  useEffect,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../modules';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { changeField, initializeForm, loginAsync } from '../../modules/auth';
// import { getAuthAsync } from '../../modules/auth';
import AuthForm from '../../components/auth/AuthForm';
import { checkAsync } from '../../modules/user';

const LoginForm: FunctionComponent<RouteComponentProps> = ({ history }) => {
  const [error, setError] = useState<string>('');

  const dispatch = useDispatch();
  const { form, auth, authError, user } = useSelector((state: RootState) => ({
    form: state.auth.login,
    auth: state.auth.auth,
    authError: state.auth.authError,
    user: state.user.user,
  }));

  //ì¸í’‹ ë³€ê²½ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬
  const onChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { value, name } = e.target;
    dispatch(
      changeField({
        form: 'login',
        key: name,
        value,
      }),
    );
  };

  //í¼ ë“±ë¡ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬
  const onSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const { username, password } = form;
    dispatch(loginAsync.request({ username, password }));
  };

  //ì»´í¬ë„ŒíŠ¸ê°€ ì²˜ìŒ ë Œë”ë§ë  ë•Œ formì„ ì´ˆê¸°í™”í•¨
  useEffect(() => {
    dispatch(initializeForm());
  }, [dispatch]);

  useEffect(() => {
    if (authError) {
      console.log('ì˜¤ë¥˜ ë°œìƒ');
      console.log(authError);
      setError('ë¡œê·¸ì¸ ì‹¤íŒ¨');
      return;
    }
    // console.log('auth:'+auth);
    if (auth) {
      console.log('ë¡œê·¸ì¸ ì„±ê³µ');
      dispatch(checkAsync.request(''));
    }
  }, [auth, authError, dispatch]);

  useEffect(() => {
    if (user) {
      history.push('/');
      try {
        localStorage.setItem('user', JSON.stringify(user));
      } catch (e) {
        console.log('localStorage is not working');
      }
    }
  }, [history, user]);

  return (
    <AuthForm
      type="login"
      form={form}
      onChange={onChange}
      onSubmit={onSubmit}
      error={error}
    />
  );
};

export default withRouter(LoginForm);
