import {
  ActionType,
  createAction,
  createAsyncAction,
  createReducer,
} from 'typesafe-actions';
import { call, put, takeLatest } from 'redux-saga/effects';
import { AxiosError } from 'axios';
import * as authAPI from '../../lib/api/auth';
import { UserInfo } from '../../lib/api/auth';
import { finishLoading, startLoading } from '../loading/loading';

const CHECK = 'user/CHECK';
const CHECK_SUCCESS = 'user/CHECK_SUCCESS';
const CHECK_FAILURE = 'user/CHECK_FAILURE';
const LOGOUT = 'user/LOGOUT';
const TEMP_SET_USER = 'user/TEMP_SET_USER'; // ìƒˆë¡œê³ ì¹¨ ì´í›„ ì„ì‹œ ë¡œê·¸ì¸ ì²˜ë¦¬

export const checkAsync = createAsyncAction(
  CHECK,
  CHECK_SUCCESS,
  CHECK_FAILURE,
)<string, UserInfo, AxiosError>();

export const logout = createAction(LOGOUT)();
export const tempSetUser = createAction(TEMP_SET_USER)<string | null>();

const actions = { checkAsync, logout, tempSetUser };
type UserAction = ActionType<typeof actions>;

//saga ìƒì„±
function* checkSaga(action: ReturnType<typeof checkAsync.request>) {
  yield put(startLoading(CHECK)); // ë¡œë”© ì‹œì‘
  try {
    const response: UserInfo = yield call(authAPI.check, action.payload);
    yield put(checkAsync.success(response));
  } catch (error) {
    yield put(checkAsync.failure(error as any));
  }
  yield put(finishLoading(CHECK)); // ë¡œë”© ì‹œì‘
}

function checkFailureSaga() {
  try {
    localStorage.removeItem('user'); // localStorageì—ì„œ userë¥¼ ì œê±°
  } catch (e) {
    console.log('localStorage is not working');
  }
}

function* logoutSaga() {
  try {
    yield call(authAPI.logout); // logout API í˜¸ì¶œ
    localStorage.removeItem('user'); // localStorageì—ì„œ userë¥¼ ì œê±°
  } catch (e) {
    console.log(e);
  }
}

export function* userSaga() {
  yield takeLatest(CHECK, checkSaga);
  yield takeLatest(CHECK_FAILURE, checkFailureSaga);
  yield takeLatest(LOGOUT, logoutSaga);
}

//ë¦¬ë“€ì„œ ì´ˆê¸°í™”
type UserState = {
  user: UserInfo | null;
  tempUser: string | null;
  checkError: Error | null;
};

const initialState: UserState = {
  user: null,
  tempUser: null,
  checkError: null,
};

//ë¦¬ë“€ì„œ ì •ì˜
const reducer = createReducer<UserState, UserAction>(initialState, {
  [TEMP_SET_USER]: (state, { payload: tempUser }) => ({
    ...state,
    tempUser,
  }),
  [CHECK_SUCCESS]: (state, { payload: user }) => ({
    ...state,
    user,
    checkError: null,
  }),
  [CHECK_FAILURE]: (state, { payload: error }) => ({
    ...state,
    user: null,
    checkError: error,
  }),
  [LOGOUT]: (state) => ({
    ...state,
    user: null,
  }),
});

export default reducer;
