import React, { FunctionComponent, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps, withRouter } from 'react-router';
import PostActionButtons from '../../components/post/PostActionButtons';
import PostViewer from '../../components/post/PostViewer';
import { removePost } from '../../lib/api/posts';
import { RootState } from '../../modules';
import { readPostAsync, unloadPost } from '../../modules/post';
import { setOriginalPost } from '../../modules/write';

const PostViewerContainer: FunctionComponent<RouteComponentProps> = ({
  match,
  history,
}: any) => {
  // ì²˜ìŒ ë§ˆìš´íŠ¸ë  ë•Œ í¬ìŠ¤íŠ¸ ì½ê¸° api ìš”ì²­
  const { postId } = match.params;
  const dispatch = useDispatch();
  const { post, error, loading, user } = useSelector((state: RootState) => ({
    post: state.post.post,
    error: state.post.readError,
    loading: state.post.loading,
    user: state.user.user,
  }));

  useEffect(() => {
    dispatch(readPostAsync.request(postId));
    // ì–¸ë§ˆìš´íŠ¸ë  ë•Œ ë¦¬ë•ìŠ¤ì—ì„œ í¬ìŠ¤íŠ¸ ë°ì´í„° ì—†ì• ê¸°
    return () => {
      dispatch(unloadPost());
    };
  }, [dispatch, postId]);

  const onEdit = () => {
    dispatch(setOriginalPost(post));
    history.push('/write');
  };

  const onRemove = async () => {
    try {
      await removePost(postId);
      history.push('/'); // í™ˆìœ¼ë¡œ ì´ë™
    } catch (e) {
      console.log(e);
    }
  };

  const ownPost = (user && user._id) === (post && post.user._id);

  return (
    <PostViewer
      post={post}
      loading={loading}
      error={error}
      actionButtons={ownPost && <PostActionButtons onEdit={onEdit} onRemove={onRemove}/>}
    />
  );
};

export default withRouter(PostViewerContainer);
