import React, { useEffect, useRef } from 'react';
import Quill, { QuillOptionsStatic } from 'quill';
import 'quill/dist/quill.bubble.css';
import styled from 'styled-components';
import Responsive from '../common/Responsive';
import palette from '../../lib/styles/palette';

const EditorBlock = styled(Responsive)`
  /*í˜ì´ì§€ ìœ„ì•„ë˜ ì—¬ë°± ì§€ì •*/
  padding-top: 5rem;
  padding-bottom: 5rem;
`;

const TitleInput = styled.input`
    font-size: 3rem;
    outline: none;
    padding-bottom: 0.5rem;
    border: none;
    border-bottom: 1px solid ${palette.gray[4]};
    margin-bottom: 2rem;
    width: 100%;
`;
const QuillWrapper = styled.div`
  /*ìµœì†Œ í¬ê¸° ì§€ì • ë° padding ì œê±°*/
  .ql-editor {
    padding: 0;
    min-height: 320px;
    font-size: 1.125rem;
    line-height: 1.5;
  }
  .ql-editor.ql-blank::before {
    left: 0px;
  }
`;

type quillElement = {
  current: string | Element;
  options?: QuillOptionsStatic | undefined;
  // ref?: React.LegacyRef<HTMLDivElement> | undefined
};
type quillInstance = {
  current: string | Quill;
};

const Editor = ({ title, body, onChangeField }: any) => {
  const quillElement: any = useRef(null);
  const quillInstance: any = useRef(null);

  useEffect(() => {
    quillInstance.current = new Quill(quillElement.current, {
      theme: 'bubble',
      placeholder: 'ë‚´ìš©ì„ ì‘ì„±í•˜ì„¸ìš”...',
      modules: {
        toolbar: [
          [{ header: '1' }, { header: '2' }],
          ['bold', 'italic', 'underline', 'strike'],
          [{ list: 'ordered' }, { list: 'bullet' }],
          ['blockquote', 'code-block', 'link', 'image'],
        ],
      },
    });

    //quillì— text-change ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬ ë“±ë¡
    //ì°¸ê³ : https://quilljs.com/docs/api#events
    const quill = quillInstance.current;
    quill.on(
      'text-change',
      (delta: string, oldDelta: string, source: string) => {
        if (source === 'user') {
          onChangeField({ key: 'body', value: quill.root.innerHTML });
        }
      },
    );
  }, [onChangeField]);

  const mounted = useRef(false);
  useEffect(() => {
    if (mounted.current) return;
    mounted.current = true;
    quillInstance.current.root.innerHTML = body;
  }, [body]);
  
  const onChangeTitle = (e: any) => {
    onChangeField({ key: 'title', value: e.target.value });
  };

  return (
    <EditorBlock>
      <TitleInput
        placeholder="ì œëª©ì„ ì…ë ¥í•˜ì„¸ìš”"
        onChange={onChangeTitle}
        value={title}
      />
      <QuillWrapper>
        <div ref={quillElement} />
      </QuillWrapper>
    </EditorBlock>
  );
};

export default Editor;
