import React, {
  useCallback,
  useState,
  ChangeEvent,
  FormEvent,
  useEffect,
} from 'react';
import styled from 'styled-components';
import palette from '../../lib/styles/palette';

const TagBoxBlock = styled.div`
  width: 100%;
  border-top: 1px solid ${palette.gray[2]};
  padding-top: 2rem;

  h4 {
    color: ${palette.gray[8]};
    margin-top: 0;
    margin-bottom: 0.5rem;
  }
`;

const TagForm = styled.form`
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  width: 256px;
  border: 1px solid ${palette.gray[9]};
  input,
  button {
    outline: none;
    border: none;
    font-size: 1rem;
  }
  input {
      padding: 0.5rem;
      flex: 1;
      min-width: 0;
  }
  button: {
      cursor: pointer;
      padding-right:1rem;
      padding-left:1rem;
      border:none:
      background: ${palette.gray[8]};
      color:white;
      font-weight:bold;
      $:hover {
          background: ${palette.gray[6]};
      }
  }
`;

const Tag = styled.div`
  margin-right: 0.5rem;
  color: ${palette.gray[6]};
  cursor: pointer;
  &:hover {
    opacity: 0.5;
  }
`;

const TagListBlock = styled.div`
  display: flex;
  margin-top: 0.5rem;
`;

// tag ê°’ì´ ë°”ë€” ë•Œë§Œ ë¦¬ë Œë”ë§
const TagItem = React.memo(
  ({ tag, onRemove }: { tag: string; onRemove: Function }) => (
    <Tag onClick={() => onRemove(tag)}>#{tag}</Tag>
  ),
);

// tags ê°’ì´ ë°”ë€” ë•Œë§Œ ë¦¬ë Œë”ë§
const TagList = React.memo(
  ({ tags, onRemove }: { tags: string[]; onRemove: Function }) => (
    <TagListBlock>
      {tags.map((tag) => (
        <TagItem key={tag} tag={tag} onRemove={onRemove} />
      ))}
    </TagListBlock>
  ),
);

const TagBox = ({
  tags,
  onChangeTags,
}: {
  tags: string[];
  onChangeTags: Function;
}) => {
  const [input, setInput] = useState<string>('');
  const [localTags, setLocalTags] = useState<string[]>([]);

  const insertTag = useCallback(
    (tag: string) => {
      if (!tag) return; // ê³µë°±ì´ë¼ë©´ ì¶”ê°€í•˜ì§€ ì•ŠìŒ
      if (localTags.includes(tag)) return; // ì´ë¯¸ ì¡´ì¬í•œë‹¤ë©´ ì¶”ê°€í•˜ì§€ ì•ŠìŒ
      const nextTags: string[] = [...localTags, tag];
      setLocalTags(nextTags);
      onChangeTags(nextTags);
    },
    [localTags, onChangeTags],
  );

  const onRemove = useCallback(
    (tag: string) => {
      const nextTags = localTags.filter((t) => t !== tag);
      setLocalTags(nextTags);
      onChangeTags(nextTags);
    },
    [localTags, onChangeTags],
  );

  const onChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    setInput(e.target.value);
  }, []);

  const onSubmit = useCallback(
    (e: FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      insertTag(input.trim()); // ì•ë’¤ ê³µë°±ì„ ì—†ì•¤ í›„ ë“±ë¡
      setInput(''); // input ì´ˆê¸°í™”
    },
    [input, insertTag],
  );

  // tags ê°’ì´ ë°”ë€” ë•Œ
  useEffect(() => {
    setLocalTags(tags);
  }, [tags]);

  return (
    <TagBoxBlock>
      <h4>íƒœê·¸</h4>
      <TagForm onSubmit={onSubmit}>
        <input
          placeholder="íƒœê·¸ë¥¼ ì…ë ¥í•˜ì„¸ìš”"
          value={input}
          onChange={onChange}
        />
        <button type="submit">ì¶”ê°€</button>
      </TagForm>
      <TagList tags={localTags} onRemove={onRemove} />
    </TagBoxBlock>
  );
};

export default TagBox;
